body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 28px;
  font-family: "Blogger Sans", "Arial", sans-serif;
  color: #444444;
  font-style: normal;
  background-color: #edeff6;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
/* blocks */
.block-wrapper {
  width: 320px;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative;
}
@media (min-width: 768px) {
  .block-wrapper {
    width: 768px;
    box-sizing: border-box;
  }
}
@media (min-width: 1440px) {
  .block-wrapper {
    width: 1440px;
  }
}
.page-header {
  width: 100%;
  z-index: 1000;
  background-color: #161c34;
}
.page-header__wrapper {
  padding: 25px;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.page-header--position-fixed {
  position: fixed;
  background-color: #ffffff;
}
.page-header__logo-static {
  display: block;
}
.page-header__logo-fixed {
  display: none;
}
.page-header--position-fixed .page-header__logo-static {
  display: none;
}
.page-header--position-fixed .page-header__logo-fixed {
  display: block;
}
.page-header__mobile-menu {
  padding: 0;
  border: 0;
  outline: 0;
  background: transparent;
}
.page-header__mobile-menu::before,
.page-header__mobile-menu::after {
  display: block;
  width: 25px;
  height: 3px;
  background: #ffffff;
  border-radius: 1px;
  content: "";
  transform-origin: 42%;
  transition: transform 0.3s, margin 0.1s;
}
.page-header__mobile-menu::before {
  margin-top: 0;
  margin-bottom: 5px;
}
.page-header__mobile-menu--type-close::before {
  margin: 0;
  transform: rotate(45deg);
}
.page-header__mobile-menu--type-close::after {
  transform: rotate(-45deg);
}
.page-header--position-fixed .page-header__mobile-menu::before,
.page-header--position-fixed .page-header__mobile-menu::after {
  background: #cccccc;
}
.page-header__login {
  display: none;
  width: 188px;
  height: 50px;
  box-sizing: border-box;
  border-radius: 25px;
  padding: 10px 0;
  margin-bottom: 51px;
  color: #161c35;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 700;
  background-color: #ffd74b;
}
@media (min-width: 768px) {
  .page-header {
    background-color: #192144;
  }
  .page-header--position-fixed {
    background-color: #ffffff;
  }
  .page-header__wrapper {
    padding: 40px;
    justify-content: start;
  }
  .page-header__mobile-menu {
    margin-left: 166px;
  }
  .page-header__login {
    display: block;
    margin-left: auto;
    margin-bottom: 0;
  }
  .page-header--inner {
    background-color: #161c35;
  }
}
@media (min-width: 1440px) {
  .page-header {
    background-color: #161c34;
  }
  .page-header--position-fixed {
    background-color: #ffffff;
  }
  .page-header__wrapper {
    padding: 47px 60px 11px;
    flex-wrap: nowrap;
  }
  .page-header--position-fixed .page-header__wrapper {
    padding: 20px 60px 18px;
  }
  .page-header__mobile-menu {
    display: none;
  }
  .page-header__login {
    display: none;
  }
}
.button {
  position: relative;
  padding: 22px 126px 22px 24px;
  border-radius: 10px;
  border: none;
  display: inline-block;
  box-sizing: border-box;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
  color: #1d2e5b;
  background-color: #ffd74b;
}
.button::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  right: 28px;
  top: 50%;
  margin-top: -7px;
  border: 7px solid;
  border-color: transparent transparent transparent #1d2e5b;
  border-right: 0;
}
.button:hover {
  background-color: #ffbd30;
}
.button:active {
  color: rgba(29, 46, 91, 0.3);
}
.button:active::after {
  border-color: transparent transparent transparent rgba(29, 46, 91, 0.3);
}
@media (min-width: 768px) {
  .button {
    padding: 27px 126px 27px 34px;
    border-radius: 20px;
    font-size: 24px;
    line-height: 24px;
  }
}
.main-nav {
  width: 272px;
  padding: 27px 25px 0;
  z-index: 1000000;
  overflow: auto;
  height: calc(100vh - 100px);
  background-color: #ffffff;
}
.main-nav__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 79px;
}
.main-nav__close-menu {
  width: 19px;
  height: 19px;
  margin-top: 5px;
  border: 0;
  outline: 0;
  background-color: transparent;
  background-image: url("../img/menu-tablet-mobile-close.png");
}
.main-nav__list {
  margin-bottom: 51px;
}
.main-nav__social {
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
}
.main-nav--closed {
  display: none;
  overflow: hidden;
  height: auto;
}
@media (min-width: 768px) {
  .main-nav {
    width: 690px;
    padding: 40px 40px 0 40px;
  }
  .main-nav__wrapper {
    justify-content: start;
    margin-bottom: 87px;
  }
  .main-nav__logo {
    margin-top: 5px;
  }
  .main-nav__close-menu {
    margin-top: 21px;
    margin-left: 171px;
  }
  .main-nav__list {
    margin-left: 59px;
    margin-bottom: 153px;
  }
  .main-nav__list--user {
    margin-bottom: 113px;
  }
  .main-nav__social {
    justify-content: start;
    margin-left: 85px;
    margin-bottom: 92px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    position: relative;
    width: 100%;
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: transparent;
  }
  .main-nav__wrapper {
    display: none;
  }
  .main-nav__list {
    margin: 0;
  }
  .main-nav__list--user {
    margin: 0;
  }
  .main-nav__social.social-link {
    display: none;
  }
}
.user-list {
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.user-list__link {
  color: #192144;
  font-size: 16px;
  font-weight: 700;
  line-height: 22px;
  text-transform: uppercase;
  text-decoration: none;
}
.user-list__item:last-child {
  order: -1;
}
.user-list__login {
  display: block;
  border-radius: 25px;
  padding: 10px 20px;
  margin-bottom: 51px;
  color: #161c35;
  font-size: 17px;
  line-height: 30px;
  text-align: center;
  background-color: #ffd74b;
}
.user-list__login:hover {
  background-color: #ffbd30;
}
.user-list__login:active {
  color: #b98c32;
}
.user-list__item:not(:last-child) {
  margin-bottom: 23px;
}
.user-list__item:not(:last-child) a {
  display: block;
  position: relative;
  margin-left: 61px;
}
.user-list__item:not(:last-child) a::before {
  content: "";
  width: 45px;
  height: 45px;
  display: block;
  position: absolute;
  left: -61px;
  top: -12px;
  background-color: #121936;
  border-radius: 50%;
}
.user-list__call::before {
  background: url("../img/contacts-phone.svg") 50% 50% no-repeat;
  background-size: 18px 18px;
}
.user-list__feedback::before {
  background: url("../img/contacts-mail.svg") 50% 50% no-repeat;
  background-size: 18px 18px;
}
@media (min-width: 768px) {
  .user-list__link {
    font-size: 28px;
    line-height: 28px;
  }
  .user-list__item:not(:last-child) {
    margin-bottom: 40px;
  }
  .user-list__item:not(:last-child) a {
    margin-left: 91px;
  }
  .user-list__item:not(:last-child) a::before {
    width: 68px;
    height: 68px;
    left: -91px;
    top: -19px;
    background-size: 27px 27px;
  }
  .user-list__login {
    display: none;
  }
}
@media (min-width: 1440px) {
  .user-list {
    flex-direction: row;
    align-items: center;
  }
  .user-list__item {
    margin: 0;
  }
  .user-list__item:last-child {
    order: 1;
  }
  .user-list__link {
    font-size: 0;
    line-height: 0;
  }
  .user-list__item:not(:last-child) {
    margin: 0;
  }
  .user-list__item:not(:last-child) a {
    margin: 0;
  }
  .user-list__item:not(:last-child) a::before {
    content: "";
    width: 50px;
    height: 50px;
    display: block;
    position: static;
    background-size: 18px 18px;
    background-color: #ff8d30;
    border-radius: 50%;
  }
  .user-list__item:not(:last-child) a:hover::before {
    background-color: #f26926;
  }
  .user-list__item:not(:last-child) a:active::before {
    background-color: #f26926;
    opacity: 0.6;
  }
  .user-list__call::before {
    background: url("../img/topmenu-phone.svg") 50% 50% no-repeat;
    background-size: 18px 18px;
  }
  .user-list__feedback::before {
    background-color: #ffbd30;
    background: url("../img/topmenu-mail.svg") 50% 50% no-repeat;
    background-size: 18px 14px;
  }
  .user-list__login {
    display: block;
    margin: 0;
    position: static;
    font-size: 20px;
    line-height: 30px;
    font-weight: 700;
  }
}
.site-list {
