body {
  min-width: 320px;
  margin: 0;
  padding: 0;

  font-size: 24px;
  line-height: 28px;
  font-family: "Blogger Sans", "Arial", sans-serif;
  color: @dark-text;
  font-style: normal;

  background-color: @light-blue-background;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
